.btn {
    float: right;
    color: white;
  }
.list-overflow-container {
  max-height: 720px;
  border: 1px #DDDDDD solid;
  border-radius: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-bottom: 30px;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: black;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

