/* styling for jumbotron */

.jumbotron {
    position: relative;
    width: 100%;
    height: 850px;
    padding: 0;
    font-family: "Cormorant SC";
    box-shadow: 10px 5px 20px 2px lightslategrey;
    margin-bottom: 0px;
  }
  
  .jumbotron .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: auto;
    background: -webkit-gradient(linear, left top, left bottom, from(#333), to(black));
    background: linear-gradient(#333, black);
    opacity: 0.7;
  }
  
  .jumbotron .background-image {
    height: 100%;
    background-image: url(/static/media/backgroundroad.99d8d31a.jpeg);
    background-size: cover;
  }
  
  .jumbotron .caption {
    position: absolute;
    top: 20%;
    width: 100%;
    text-align: center;
  }
  
  .jumbotron .caption h1,
  .jumbotron .caption p {
    color: #00cc7e;
  }
  
  .jumbotron h1 {
    font-size: 80px;
    font-weight: 800;
  }
  
  .jumbotron p {
    font-size: 30px;
  }

